        <!--                                       about us                                       -->
        <v-row id="header-aboutus" align="center" justify="center" style="height: 15vh" :class="headingClass.concat(' font-weight-medium font-italic text--secondary')">
          About Us
        </v-row>
        <v-row align="center" justify="center">
          <v-col :cols="$vuetify.breakpoint.name === 'lg' ? 6 : 8" style="text-align: justify">
            The Sanctuary Collection is a large property development project, made possible by successful international companies like Perennial Real Estate, Qingjian Realty Group and Cipta Harmoni Lestari, who are commited to providing a new lifestyle to Indonesia. 
          </v-col>
        </v-row>
        <v-row align="center" justify="center">
          <v-col :cols="$vuetify.breakpoint.name === 'lg' ? 8 : 10">
