        <!--                                       location                                       -->
        <v-row id="header-location" align="center" justify="center" style="height: 15vh" :class="headingClass.concat(' font-weight-medium font-italic text--secondary')">
          Location
        </v-row>
        <v-row align="center" justify="center">
          <v-col :cols="$vuetify.breakpoint.name === 'lg' ? 8 : 10">
            <v-carousel
                hide-delimiters
