                :height="windowWidth/windowHeight > 1 ? '50vh' : '20vh'">
              <v-carousel-item
                v-for="(item,i) in projectItems"
                :key="i"
                :src="item.src"
                contain
                reverse-transition="fade-transition"
                transition="fade-transition"
              ></v-carousel-item>
            </v-carousel>
          </v-col>
        </v-row>
        <v-divider
            class="my-4"
          ></v-divider>
        <!--                                       unit types                                       -->
        <v-row id="header-unittypes" align="center" justify="center" style="height: 15vh" :class="headingClass.concat(' font-weight-medium font-italic text--secondary')">
          Unit Types
        </v-row>
        <v-row align="center" justify="center">
          <v-col :cols="$vuetify.breakpoint.name === 'lg' ? 8 : 10">
            <v-carousel
                hide-delimiters
                progress
                :height="windowWidth/windowHeight > 1 ? '50vh' : '20vh'">
              <v-carousel-item
                v-for="(item,i) in typeItems"
                :key="i"
                :src="item.src"
                contain
                reverse-transition="fade-transition"
                transition="fade-transition"
              ></v-carousel-item>
            </v-carousel>
          </v-col>
        </v-row>
        <v-divider
            class="my-4"
          ></v-divider>

        <!--                                       price list                                       -->
        <v-row id="header-paymentplans" align="center" justify="center" style="height: 15vh" :class="headingClass.concat(' font-weight-medium font-italic text--secondary')">
          Payment Plans
        </v-row>
        <v-row align="center" justify="center">
          <v-col :cols="$vuetify.breakpoint.name === 'lg' ? 8 : 10">
            <v-img src="../public/price.png" aspect-ratio="1.7" contain></v-img>
          </v-col>
        </v-row>

        
      </v-container>
    </v-main>

    <!--                                       music player                                       -->
    <div class="text-center">
      <v-bottom-sheet inset hide-overlay>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            fab
            fixed
            bottom
            left
            color="red"
            dark
            v-bind="attrs"
            v-on="on"
          >
            <v-icon>fa-music</v-icon>
          </v-btn>
        </template>
        <v-card tile>
          <v-progress-linear
            :value="songProgress"
            class="my-0"
            height="3"
          ></v-progress-linear>
          <v-list>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>{{ songs[activeSong].title }}</v-list-item-title>
                <v-list-item-subtitle>{{ songs[activeSong].singer }}</v-list-item-subtitle>
              </v-list-item-content>
              <v-spacer></v-spacer>
              <v-list-item-icon>
                <v-btn icon  @click="previousSong">
                  <v-icon>mdi-rewind</v-icon>
                </v-btn>
              </v-list-item-icon>
              <v-list-item-icon :class="{ 'mx-5': $vuetify.breakpoint.mdAndUp }">
                <v-btn icon @click="playPause">
                  <v-icon v-if="play">mdi-pause</v-icon>
                  <v-icon v-else>mdi-play</v-icon>
                </v-btn>
              </v-list-item-icon>
              <v-list-item-icon
                class="ml-0"
                :class="{ 'mr-3': $vuetify.breakpoint.mdAndUp }"
              >
                <v-btn icon @click="nextSong">
                  <v-icon>mdi-fast-forward</v-icon>
                </v-btn>
              </v-list-item-icon>
            </v-list-item>
          </v-list>
        </v-card>
      </v-bottom-sheet>
    </div>

    <!--                                       up down                                       -->
    <v-btn v-if="!(this.windowWidth > 800)" style="bottom: 15vh"
      small
      fab
      fixed
      bottom
      right
      color="primary"
      dark
      @click="scrollUp"
    >
      <v-icon>fa-arrow-up</v-icon>
    </v-btn>
    <v-btn v-if="!(this.windowWidth > 800)"
      small
      fab
      fixed
      bottom
      right
      color="primary"
      dark
      @click="scrollDown"
    >
      <v-icon>fa-arrow-down</v-icon>
    </v-btn>
  </v-app>
</template>

<script>


export default {
  name: 'App',
  data: () => ({
    offsetTop: 0,
    phoneNumber: '+6281283849479',
      phoneMessage: 'Hi Ruth, I would like to more about the Sanctuary Collection :)',
      email: 'ruthveronica_p@yahoo.com',
      emailMessage: 'Hi Ruth, I would like to more about the Sanctuary Collection :)',
      instagram: 'https://www.instagram.com/sanctuary_ruth/',
      facebook: 'https://www.facebook.com/ruthveronica.f.panggabean',
      popup: false,
      locationItems: [
        { src: require('../public/location/location1.png') },
        { src: require('../public/location/location2.png') },
        { src: require('../public/location/location3.png') },
        { src: require('../public/location/location4.png') },
        { src: require('../public/location/location5.png') },
        { src: require('../public/location/location6.png') },
        { src: require('../public/location/location7.png') },
        { src: require('../public/location/location8.png') },
        { src: require('../public/location/location9.png') },
        { src: require('../public/location/location10.png') },
        { src: require('../public/location/location11.png') },
        { src: require('../public/location/location12.png') },
        { src: require('../public/location/location13.png') }
      ],
      projectItems: [
        { src: require('../public/project/project1.png') },
        { src: require('../public/project/project2.png') },
        { src: require('../public/project/project3.png') },
        { src: require('../public/project/project4.png') },
        { src: require('../public/project/project5.png') },
        { src: require('../public/project/project6.png') }
      ],
      typeItems: [
        { src: require('../public/types/type1.png') },
        { src: require('../public/types/type2.png') },
        { src: require('../public/types/type3.png') },
        { src: require('../public/types/type4.png') }
      ],
      play: false,
      songs: [
        { src: require('../public/songs/song1.mp3'), title: 'New Light', singer: 'John Mayer', length: 216 }
      ],
      activeSong: 0,
      songProgress: 0,
      windowWidth: 0,
      windowHeight: 0
  }),
  computed: {
    color: function() {
      if (this.offsetTop === 0) return 'transparent'
      else return 'primary'
    },
    headingClass: function() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 'text-h4'
        case 'sm': return 'text-h3'
        case 'md': return 'text-h2'
        case 'lg': return 'text-h1'
        case 'xl': return 'text-h1'
        default: return 'text-h3'
      }
    },
    audio: function() {
      return new Audio(this.songs[this.activeSong].src)
    }
  },
  methods: {
    onScroll (e) {
      this.offsetTop = e.target.scrollingElement.scrollTop
    },
    scrollUp () {
      if (this.offsetTop-1 < document.getElementById('header-contact').offsetTop) {
        this.$vuetify.goTo(0)
      } else if (this.offsetTop-1 < document.getElementById('header-aboutus').offsetTop) {
        this.$vuetify.goTo('#header-contact')
      } else if (this.offsetTop-1 < document.getElementById('header-location').offsetTop) {
        this.$vuetify.goTo('#header-aboutus')
      } else if (this.offsetTop-1 < document.getElementById('header-project').offsetTop) {
        this.$vuetify.goTo('#header-location')
      } else if (this.offsetTop-1 < document.getElementById('header-unittypes').offsetTop) {
        this.$vuetify.goTo('#header-project')
      } else if (this.offsetTop-1 < document.getElementById('header-paymentplans').offsetTop) {
        this.$vuetify.goTo('#header-unittypes')
      } else {
        this.$vuetify.goTo('#header-paymentplans')
      }
    },
    scrollDown () {
      if (this.offsetTop+1 > document.getElementById('header-unittypes').offsetTop) {
        this.$vuetify.goTo('#header-paymentplans')
      } else if (this.offsetTop+1 > document.getElementById('header-project').offsetTop) {
        this.$vuetify.goTo('#header-unittypes')
      } else if (this.offsetTop+1 > document.getElementById('header-location').offsetTop) {
        this.$vuetify.goTo('#header-project')
      } else if (this.offsetTop+1 > document.getElementById('header-aboutus').offsetTop) {
        this.$vuetify.goTo('#header-location')
      } else if (this.offsetTop+1 > document.getElementById('header-contact').offsetTop) {
        this.$vuetify.goTo('#header-aboutus')
      } else {
        this.$vuetify.goTo('#header-contact')
      }
    },
    sendWhatsapp () {
      window.open(
          `https://api.whatsapp.com/send?phone=${this.phoneNumber}&text=${this.phoneMessage}`,
          "_blank"
        );
    },
    sendMail () {
      window.open(`mailto:${this.email}?subject=subject&body=${this.emailMessage}`);
    },
    openInstagram () {
      window.open(this.instagram);
    },
    openFacebook () {
      window.open(this.facebook);
    },
    previousSong () {
      this.audio.pause()
      if (this.songProgress < 10) {
        this.audio.currentTime = 0
      } else {
        if (this.activeSong === 0) this.activeSong = this.songs.length - 1
        else this.activeSong--
      }
      if (this.play) this.audio.play()
      this.songProgress = 0
    },
    nextSong () {
      this.audio.pause()
      if (this.activeSong === this.songs.length - 1) this.activeSong = 0
      else this.activeSong++
      this.songProgress = 0
      if (this.play) this.audio.play()
    },
    playPause () {
      this.play = !this.play
      if (this.play) {
        this.audio.play()
        setTimeout(this.songCounter, 1000)
      } else {
        this.audio.pause()
      }
    },
    songCounter () {
      if (!this.play) return
      const newProgress = this.songProgress + 100 / this.songs[this.activeSong].length
      if (newProgress > 100) this.songProgress = 100
      else this.songProgress  = newProgress
      setTimeout(this.songCounter, 1000)
    },
    onResize () {
      this.windowWidth = window.innerWidth
      this.windowHeight = window.innerHeight
    }
  },
  mounted () {
    this.onResize()
    window.addEventListener('resize', this.onResize, { passive: true })
  }
};
</script>

<style>
#app {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
  overflow-x: hidden;
}
</style>